import styled, { keyframes } from 'styled-components';
import { Parallax, Background } from 'react-parallax';

const upAndDown = keyframes`
    0%{
        transform: translateY(10px) rotate(-90deg);
    }
    50%{
        transform: translateY(0px) rotate(-90deg);
    }
    100%{
        transform: translateY(10px) rotate(-90deg);
    }
`;

const upAndDown2 = keyframes`
    0%{
        transform: translateY(15px) rotate(-90deg);
    }
    50%{
        transform: translateY(0px) rotate(-90deg);
    }
    100%{
        transform: translateY(15px) rotate(-90deg);
    }
`;

export const Wrapper = styled.div`
    background: #000000;
    padding: 60px 0 100px 0;
    min-height: 100vh;
    z-index: 1;

    @media screen and (max-width: 990px){
        padding: 80px 0 100px 0; 
    }
`;

export const Form = styled.form`
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
`;

export const ImageBlock = styled.div`
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

export const Image = styled.div`
    background: url(${props => props.sourceUrl || ''});
    width: 100vw;
    height: calc(100vh - 50px);
    background-repeat: repeat;
    background-position: center center;
    background-attachment: fixed;
    background-size: cover;
    transition: 0.7s;
    opacity: 1;

    @media screen and (max-width: 730px){
        background-attachment: scroll;
    }

    &.active{
        opacity: 0;
    }
`;

export const TitleWrap = styled.div`
    margin-top: -50px;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: row wrap;
    text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
    width: 100%;

    @media screen and (max-width: 730px){
        margin-top: -70px;
    }
`;

export const Logo = styled.img`
    width: 100px;

    @media screen and (max-width: 730px){
        display: none;
    }
`;

export const Moto = styled.p`
    color: white;
    font-weight: 700;
    text-align: center;
    font-size: 80px;
`;

export const TitleLogo = styled.span`
    font-weight: 900;
    font-style: italic;
    display: none;

    @media screen and (max-width: 730px){
        display: inline;
    }
`;

export const TitleDate = styled.sup`
    font-size: 20px;
    color: white;
    font-weight: 700;
    text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
    margin: -10px 0 0 10px;
    opacity: 1;
    transition: 0.7s;
    
    &.active{
        opacity: 0;
    }

    @media screen and (max-width: 839px){
        margin-top: 10px;
        font-size: 23px;
    }
`;

export const Title = styled.p`
    position: absolute;
    color: white;
    font-weight: lighter;
    text-align: center;
    font-weight: 100;
    margin-top: 70vh;
    color: rgba(255,255,255,0.8);
    text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.7);
    font-size: 20px;
    opacity: 1;
    transition: 0.7s;
    
    &.active{
        opacity: 0;
    }

    @media screen and (max-width: 839px){
        margin-top: 300px;
    }
`;

export const SearchWrapper = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100vw;
    background: black;
    border-top: 10px solid rgba(0, 0, 0, 1);
        padding-top: 10px;
`;

export const Container = styled.div`
    display: flex;
    justify-content: center;
`;

export const ContainerBottom = styled.label`
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
`;

export const Input = styled.input`
    width: 200px;
    border: none;
    padding:0 35px 0 20px;
    font-size: 16px;
    color: white;
    background: transparent;

    &:hover{
        color: red;
    }


    @media screen and (max-width: 730px){
        font-size: 30px;
        width: 66vw;
        font-weight: bold;
    }
`;

export const Button = styled.button`
    width:100px;
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    font-weight: bold;
    color: white;
    border: 1px solid red;
    border-radius: 30px;
    margin-right: 15px;
    padding: 10px 20px;
    transition: 0.3s;
    background: #000;

    &:hover, &:active{
            color: black;
            background: red;
    }

    @media screen and (max-width: 730px){
        font-size: 30px;
        width: 33vw;
    }
`;

export const TextInfo = styled.p`
    color: rgba(255,255,255,0.9);
    font-size: 20px;
    font-weight: 100;
    letter-spacing: 2px;
    margin-top: 20px;
`;

export const Arrow = styled.img`
    width: 20px;
    animation: ${upAndDown} 2s linear infinite;
`;

export const ArrowBottom = styled(Arrow)`
    margin-top: -5px;
    transform: translateY(-10px) rotate(-90deg);
    animation: ${upAndDown2} 2s linear infinite;
`;