import React,{useState} from 'react';
import { Navbar, Content,LinkWrapper,TriggerLine1,TriggerLine2,TriggerLine3,Trigger,ContentItemMiddle,Navigation,LogoItem, ContentLink, LogoImage } from './styles';

export const Navbar1 = () => {
    const [isActive, setIsActive] = useState(true);
    const [isActive2, setIsActive2] = useState(true);
    
    const triggerHandler = () => {
        if(isActive){
            setIsActive(false);
            setIsActive2(false);
        } else {
            setIsActive(true);
            setIsActive2(true);
        } 
    }

    const closeMenu = () =>{
        setIsActive(true);
        setIsActive2(true);
    }

    return (
        <Navbar>
            <Navigation> 
                <Content>
                    <LogoItem to='/' onClick={closeMenu, ()=>{window.scrollTo(0,0)}}>
                        <LogoImage src="https://upload.wikimedia.org/wikipedia/commons/thumb/a/a3/NASA_Worm_logo.svg/1200px-NASA_Worm_logo.svg.png" alt="Logo" />
                    </LogoItem>
                    <Trigger onClick={triggerHandler}>
                        <TriggerLine1 className={isActive ? '' : 'active'}></TriggerLine1>
                        <TriggerLine2 className={isActive ? '' : 'active'}></TriggerLine2>
                        <TriggerLine3 className={isActive ? '' : 'active'}></TriggerLine3>
                    </Trigger>
                    <LinkWrapper className={isActive ? 'active' : ''}>
                        <ContentItemMiddle>
                            <ContentLink to="/" onClick={closeMenu}>Photo of the day</ContentLink>
                            <ContentLink to="/weather" onClick={closeMenu}>Weather on Mars</ContentLink>
                            <ContentLink to="/gallery" onClick={closeMenu}>NASA Gallery</ContentLink>
                        </ContentItemMiddle>
                    </LinkWrapper>
                </Content>
            </Navigation>
        </Navbar>
    )
}
