import { createGlobalStyle } from 'styled-components';

export const GlobalStyle = createGlobalStyle`
    *{
        box-sizing: border-box;
        margin: 0;
        padding: 0;
        outline: none;
        font-family: 'Source Sans Pro', sans-serif;
        text-decoration: none;
        scroll-behavior: smooth;
        cursor: none;
        user-select: none;
    }
    body{
        background: #000;
    }
`;