import styled, { keyframes } from 'styled-components';
import {
    Link
} from "react-router-dom";

const changeColor = keyframes`
  0%{
      color: #fff;
        border-bottom: 4px solid white;
  }
  50% {
      color: #3293dd;
        border-bottom: 4px solid #3293dd;
  }
  100%{
      color: #fff;
        border-bottom: 4px solid white;
  }
`;

export const Navbar = styled.div`
    top: 0;
    background: #000000;
    padding: 0 40px;
    height: 60px;
    display: flex;
    align-items: center;
    position: fixed;
    width: 100%;
    z-index: 100;
    
    @media screen and (max-width: 990px){
        padding: 0;   
        height: 80px;
    }
`;

export const Content = styled.ul`
    display: flex;
    justify-content: space-between;
    list-style-type: none;
`;

export const Navigation = styled.nav`
    width: 100%;
`;

export const ContentItem = styled.li`
    display: flex;
    align-items: center;
`;

export const ContentItemMiddle = styled(ContentItem)`
    margin-right: 50px;

    @media screen and (max-width: 1060px){
        margin-right: 20px;    
    }

    @media screen and (max-width: 990px){
        margin-right: 0px;  
        flex-direction: column; 
        align-items: flex-start;
        padding-left: 25px;
    }
`;

export const LogoItem = styled(Link)`
    display: flex;
    align-items: center;
    
    @media screen and (max-width: 990px){
        margin-left: 25px;    
    }
`;

export const LogoImage = styled.img`
    width: 100px;
`;

export const LogoText = styled.p`
    font-weight: 900;
    color: white;
    font-size: 23px;
    margin-left: 10px;
    transition: 0.15s;

    &:hover{
        color: #3293dd;
    }
`;

export const ContentLink = styled(Link)`
    color: white;
    margin: 0 20px;
    height: 150%;
    display: flex; 
    align-items: center;
    transition: 0.15s;
    font-weight: bold;

    &:hover{
        animation: ${changeColor} 1.5s linear infinite;
    }

    @media screen and (max-width: 990px){
        margin: 30px 0;
        font-size: 20px;
    }    
`;

export const LinkWrapper = styled.div`
    display: flex;
    
    @media screen and (max-width: 990px){
        flex-direction:column;
        margin-top: 60px;
        width: 300px;
        height: 100vh;
        background: #000000;
        top: 0;
        position: absolute;
        transition: 0.2s ease;

        &.active {
            transform: translate(-100%);
            /* opacity: 0;  */
            visibility: hidden;
        }
    }

    @media screen and (max-width: 450px){
        width: 100vw;
    }
`;

export const Trigger = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 5px 25px 0 0;
    color: white;
    width: 30px;
    height: 30px;
    
    @media screen and (min-width: 990px){
        display: none;
    }
`;

export const TriggerLine = styled.div`
    width: 100%;
    height: 3px;
    background: #fff;
    transition: 0.1s;
`;

export const TriggerLine1 = styled(TriggerLine)`
    &.active{
        transform:translateY(14px) rotate(45deg); 
        transition: 0.1s;
    }
`;
export const TriggerLine2 = styled(TriggerLine)`
    &.active{
        transform: scale(0);
        transition: 0.3s;
    }
`;
export const TriggerLine3 = styled(TriggerLine)`
    &.active{
        transform:translateY(-13px) rotate(-45deg); 
        transition: 0.1s;
    }
`;

