import { APOD } from "../APOD";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import { GlobalStyle } from "./styles";
import { Navbar1 } from '../Navbar1';
import AnimatedCursor from "react-animated-cursor"

const Weather = () => {
  return <h1 style={{color: "white", marginTop: "300px"}}>Here will be weather on Mars</h1>
}

const Gallery = () => {
  return <h1 style={{color: "white", marginTop: "300px"}}>Here will be Nasa gallery</h1>
}

const App = () => {
  return (
    <div className="App">

      <GlobalStyle />
      <AnimatedCursor
        innerSize={15}
        outerSize={15}
        color='255, 255, 255'
        outerAlpha={0.2}
        innerScale={0.7}
        outerScale={3}
      />

      <Router>
        <Navbar1 />
        <Switch>
          <Route path="/weather">
            <Weather />
          </Route>
          <Route path="/gallery">
            <Gallery />
          </Route>
          <Route path="/">
            <APOD />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
